a {
  text-decoration: none;
  color: #000000;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

[data-tabs=parent].no-transition > [data-tabs=control],
[data-tabs=parent].no-transition > [data-tabs=content],
[data-tabs=parent].no-transition > [data-tabs=element] {
  transition: none;
}
[data-tabs=parent].no-transition-global [data-tabs=control],
[data-tabs=parent].no-transition-global [data-tabs=content],
[data-tabs=parent].no-transition-global [data-tabs=element] {
  transition: none;
}

[data-tabs=parent] [data-tabs=content] {
  position: relative;
}

[data-tabs=element] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs=element].for-load {
  position: static;
  visibility: visible;
  opacity: 1;
  transition: none;
}

[data-tabs=element].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

.page__body {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  width: 320px;
  min-height: 100%;
  margin: 0 auto;
  color: #102654;
  display: flex;
  flex-direction: column;
}
.page__body .header, .page__body .footer {
  flex-shrink: 0;
}
.page__body main {
  flex-grow: 1;
}

@media screen and (min-width: 768px) {
  .page__body {
    width: 768px;
  }
}
@media screen and (min-width: 1200px) {
  .page__body {
    width: 1366px;
  }
}
.header {
  background: conic-gradient(from 176deg at 20.7% 35.74%, #003D85 0deg, #B80027 262deg, #003D85 358deg);
}

.header__container {
  width: 290px;
  margin: 0 auto;
  margin-top: 15px;
}

.header__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.hero-block__image {
  object-fit: cover;
}

.header__logo {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 20px;
  line-height: 24px;
  font-weight: 700;
  font-style: normal;
  color: #ffffff;
}

.header__tel {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 20px;
  line-height: 24px;
  font-weight: 600;
  font-style: normal;
  color: #ffffff;
}

@media screen and (max-width: 767px) {
  .hero-block__image {
    display: none;
  }
}
@media screen and (min-width: 768px) {
