.gym__video-wrapper-decoration {
  width: 320px;
  height: 167px;
  background-color: rgba(20, 54, 122, 0.5);
}

.gym__video-player-button-decoration-ring {
  width: 96px;
  height: 96px;
  background: linear-gradient(white, white) padding-box, linear-gradient(83deg, #5885b8 0%, #c77a8a 88.54%) border-box;
  border-radius: 50px;
  border: 2px solid transparent;
}

.gym__video-player-button-svg {
  position: absolute;
  left: 31px;
  bottom: 27px;
  fill: #ffffff;
  width: 12px;
  height: 12px;
}

.gym__video-player-button-decoration-circle {
  width: 72px;
  height: 72px;
  border-radius: 50px;
  background: linear-gradient(83deg, #003D85 0%, #B80027 88.54%);
  position: absolute;
  left: 14px;
  bottom: 14px;
}

.gym__video-player-button {
  position: relative;
}
.gym__video-player-button:hover .gym__video-player-button-decoration-ring {
  background: linear-gradient(white, white) padding-box, linear-gradient(#102654) border-box;
}
.gym__video-player-button:hover .gym__video-player-button-decoration-circle {
  background: #ffffff;
}
.gym__video-player-button:hover .gym__video-player-button-svg {
  fill: #102654;
}

